class App extends Component {
  render() {
    const { match } = this.props;

    return (
      <AppLayout>
        <div className="dashboard-wrapper">
          <Suspense fallback={<div className="loading" />}>
            <Switch>
              <Redirect
                exact
                from={`${match.url}/`}
                to={`${match.url}/testCenters`}
              />
              <Route
                path={`${match.url}/dashboard`}
                render={(props) => <Dashboard {...props} />}
              />
              <Route
                path={`${match.url}/promotions`}
                render={(props) => <Promotions {...props} />}
              />
              <Route
                path={`${match.url}/testCenters`}
                render={(props) => <Issuers {...props} />}
              />
              <Route
                path={`${match.url}/prefunds`}
                render={(props) => <Prefunds {...props} />}
              />
              <Route
                path={`${match.url}/caribbeanCricket`}
                render={(props) => <CaribbeanCricket {...props} />}
              />
              <Route
                path={`${match.url}/hubs`}
                render={(props) => <Hubs {...props} />}
              />
              <Route
                path={`${match.url}/languages`}
                render={(props) => <Languages {...props} />}
              />
              <Route
                path={`${match.url}/operators`}
                render={(props) => <Operators {...props} />}
              />
              <Route
                path={`${match.url}/shops`}
                render={(props) => <Shops {...props} />}
              />
              <Route
                path={`${match.url}/regions`}
                render={(props) => <Regions {...props} />}
              />
              <Route
                path={`${match.url}/users`}
                render={(props) => <Users {...props} />}
              />
              <Route
                path={`${match.url}/products`}
                render={(props) => <Products {...props} />}
              />
              <Route
                path={`${match.url}/sellerproducts`}
                render={(props) => <SellerProducts {...props} />}
              />
              <Route
                path={`${match.url}/categories`}
                render={(props) => <Categories {...props} />}
              />
              <Route
                path={`${match.url}/brands`}
                render={(props) => <Brands {...props} />}
              />
              <Route
                path={`${match.url}/logs`}
                render={(props) => <Logs {...props} />}
              />
              <Route
                path={`${match.url}/orders`}
                render={(props) => <Orders {...props} />}
              />
              {/* <Redirect to="/error" /> */}
            </Switch>
          </Suspense>
        </div>
      </AppLayout>
    );
  }
}
const mapStateToProps = ({ menu }) => {
  const { containerClassnames } = menu;
  return { containerClassnames };
};

export default withRouter(connect(mapStateToProps, {})(App));
